.skills {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-bottom: 50px;
}

.skills-section {
    display: flex;
    justify-content: space-between;
    padding: 20px 0px;
}

.skills-text {
    font-size: 20px;
    padding-bottom: 22px;
}

.skills-section-title {
    font-size: 25px;
    font-weight: 700;
}

.skills-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 70%;
    justify-content: flex-end;
}


@media only screen and (max-width: 720px) {
    .skills-list {
        max-width: 100%;
        justify-content: center;
    }

    .skills-section {
        flex-direction: column;
        align-items: center;
    }
    .skills-section-title {
        font-size: 23px;
    }
    .section-title label{
        font-size: 17px;
    }
    .skills-text {
        font-size: 18px;
    }
}